*{
    margin: 0px;
    font-family: inter;
}

html{
    background: rgb(35, 36, 44)
}
.appContainer{
    background: rgb(21, 23, 32);
    width: 530px;
    margin: 50px auto;
    height: 700px;
    padding: 30px;
    border-radius: 10px;
    color: rgb(172, 179, 221); 
    display: flex;
    align-items: center;
    justify-content: center;
    box-shadow: 0 3px 5px rgba(0,0,0,0.4);
    position: relative;
    overflow: hidden;
    button{
        z-index: 2;
        cursor: pointer;
        color: rgb(146, 154, 197);
        padding: 5px 20px;
        border-radius: 10px;
        background: rgb(28, 30, 43);
        border: none;
        box-shadow: 0 3px 5px rgba(0,0,0,0.3);
    }
}

.backgroundSVG{
    position: absolute;
    height: 100%;
    width: 100%;
    z-index: 0;
    bottom: 0px;
    #path1, #path2{
        fill: rgb(21, 23, 32);
    }
    #path1{
        transform: scale(1, 1.5);
        width: 50%;
    }
    #path2{
        transform: translate(400px, -250px);
    }
    &.quizStarted > #path1, &.quizStarted > #path2{
        opacity: 0;
        animation: revealTriviaSVG .5s forwards ease-in;
        fill: rgb(26, 28, 37);
    }
    @keyframes revealTriviaSVG{
        from{
            opacity: 0;
        }
        to{
            opacity: 1;
        }

    }
}

.quizzicalOpening{
    display: flex;
    flex-direction: column;
    justify-content: center;
    z-index: 1;
    *{
        text-align: center;
        color: rgb(183, 191, 237);
    }
    .openingTitle{ 
        font-size: 62px;
        font-weight: 400;
        text-shadow: 0 3px 5px black;
    }
    .openingPrompt{
        margin-bottom: 35px;
    }
    .openingButton{
        margin: auto;
        height: 50px;
        width: 160px;
        border-radius: 10px;
        font-size: 22px;
        background: rgb(42, 46, 67);
        border: none;
        box-shadow: 0 3px 5px rgba(0,0,0,0.3);
        &:hover{
            cursor: pointer;
            animation: glowStartButton .15s forwards
        }
    }
    @keyframes glowStartButton{
        from{
            color: rgb(183, 191, 237);
            background: rgb(42, 46, 67);
            box-shadow: 0 3px 5px rgba(0,0,0,0.3);
        }
        to{            
            color: rgb(200, 209, 255);
            background: rgb(50, 55, 80);;
            box-shadow: 0 3px 5px rgba(0,0,0,0.1);
        }
    }
}
.triviaContainer{
    z-index: 1;
    height: 100%;
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    form{
        display: grid;
        grid-template-columns: auto auto;
        width: 100%;
        align-items: center;
        justify-content: space-evenly;
        h1{
            grid-area: 2/1/3/3;
            font-size: 32px;
            margin: 40px 0px;
            font-weight: 400;
            text-align: center;
        }
        button{
            color: rgb(183, 191, 237);
            background: rgb(26, 28, 40);
            font-size: 20px;
            height: 250px;
            width: 180px;
            border: none;
            border-radius: 10px;
            box-shadow: 0 3px 5px rgba(0,0,0,0.3);
            cursor: pointer;
            user-select: none;
            &:hover{
                animation: hoveredCategoryButton .7s forwards;
            }
            &:active{
                animation: clickedCategoryButton .1s forwards;
            }
        }
    }
    .closingButton{
        margin-top: 20px;
        &:hover{
            color: rgb(202, 209, 252);
            background: rgb(39, 42, 60);
        }
    }
}
@keyframes clickedCategoryButton{
    from{
        color: rgb(212, 219, 252);
        background: rgb(31, 34, 49);
        text-shadow: 0 0 2px  rgb(212, 219, 252);
        box-shadow: 0 2px 3px rgba(0,0,0,0.2);
        transform: scale(1.02);
        font-size: 21px;
    }
    to{
        color: rgb(230, 234, 254);
        background: rgb(38, 41, 60);
        text-shadow: 0 0 2px  rgb(212, 219, 252);
        box-shadow: 0 2px 3px rgba(0,0,0,0.2);
        transform: scale(1);
        font-size: 20px;
    }
}
@keyframes hoveredCategoryButton{
    from{
        color: rgb(183, 191, 237);
        box-shadow: 0 3px 5px rgba(0,0,0,0.3);
        transform: scale(1);
        font-size: 20px;
    }
    to{
        color: rgb(212, 219, 252);
        text-shadow: 0 0 2px  rgb(212, 219, 252);
        box-shadow: 0 2px 3px rgba(0,0,0,0.2);
        transform: scale(1.02);
        font-size: 21px;
    }
}
.triviaContainer.wQuestions{
    flex-direction: column;
    .questionCategory{
        color: white;
        font-weight: bolder;
        margin-bottom: 10px;
        padding-bottom: 10px;
        border-bottom: solid 2px rgb(41, 45, 62);
        width: 100%;
        text-align: center;
        font-size: 22px;
    }
    .questionsArray{
        flex-direction: column;
        width: 100%;
        height: 100%;
        padding-top: 10px;
        overflow-y: scroll;
        .questionContainer{
            padding: 0px 0px;
            margin-bottom: 20px;
            border-bottom: solid 2px rgb(68, 78, 113);
            width: 100%;
            .questionPrompt{
                color: rgb(222, 227, 252);
                margin-bottom: 20px;
            }
            .answer{
                padding: 5px 10px;
                border-radius: 15px;
                border: none;
                margin-right: 10px;
                margin-bottom: 10px;
                color: rgb(186, 194, 232);
                background: rgb(38, 42, 61);
                border: solid 2px rgb(36, 41, 59);
                cursor: pointer;
                &:hover{
                    color: rgb(236, 239, 255);
                    background: rgb(70, 78, 110);
                    border: solid 2px rgb(67, 75, 107);
                }
            }
            .selected{
                color: white;
                text-shadow: 0 0 2px white;
                background: rgb(93, 103, 143);
                border: solid 2px rgb(220, 223, 239);
                &:hover{
                    color: white;
                    text-shadow: 0 0 2px white;
                    background: rgb(93, 103, 143);
                    border: solid 2px rgb(106, 116, 159);
                }
            }

            .locked{
                color: rgb(130, 137, 174); 
                pointer-events: none;
            }
            .correct{
                color: rgb(25, 39, 25);
                background: rgb(93, 145, 93);
                &.chosen{
                    color: rgb(243, 255, 243);
                    border: solid 2px white;
                }
            }
            .incorrect{
                background: rgb(176, 94, 94);
                color: rgb(255, 223, 223);
                border: solid 2px white;
            }

        }
        > :last-child{
            border-bottom: none;
        }
        &::-webkit-scrollbar{
            width: 5px;
            background-color: transparent;
            margin-left: 10px;
        }
        &::-webkit-scrollbar-thumb{
            background: rgb(93, 103, 143);
            border-radius: 10px;
        }

        
    }
    .questionEndingButtons{
        margin-top: 10px;
        padding-top: 10px;
        border-top: solid 2px rgb(41, 45, 62);;
        width: 100%;
        display: flex;
        align-items: center;
        justify-content: space-between;
        button{
            color: rgb(183, 191, 237);
            background: rgb(35, 38, 54);
            font-size: 18px;
            height: 35px;
            width: 138px;
            border: none;
            border-radius: 10px;
            cursor: pointer;
            user-select: none;
        }
        .hidden{
            opacity: 0;
            pointer-events: none;
        }
    }
}
